import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { Chat } from 'src/models/chat.model';
import { ChatService } from '../services/chat.service';
import { FormularioService } from '../services/formulario.service';
declare var window: any;

declare var responsiveVoice;

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss']
})
export class ChatComponent implements OnInit {

  mensaje: Array<Chat>;
  account: any = {};
  chat = false;
  form = true;

  speak:boolean = false;

  uId: string;
  mail: string;
  name: string;
  node = '';

  text: any;
  constructor(public chatService: ChatService, private signupService: FormularioService) {
  }

  ngOnInit() {
    this.mensaje = [];

    // const scrolling = document.getElementById('chat-box-body');

    // scrolling.scrollTop = scrolling.scrollHeight;

  }




  // speack

  speackToText() {
    window.SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;

    const recognition = new SpeechRecognition();
    recognition.interimResults = true;


    recognition.addEventListener('result', e => {

      const trascript = Array.from(e.results)
        .map(result => result[0])
        .map(result => result.transcript)
        .join('');

      this.text = trascript;


      console.log(trascript);
    });


    recognition.addEventListener('end', recognition.stop);

    recognition.start();

    this.text = '';

  }


  // formulario

  doSignup(terminos: boolean) {


    console.log(this.account);

    if (terminos === true && this.account.Name !== '' && this.account.Phone !== '' && this.account.Email !== '') {

      localStorage.setItem('name', this.account.Name);
      localStorage.setItem('phone', this.account.Phone);

      this.signupService.Signup(this.account);
      this.chat = true;
      this.form = false;

      const email = this.account.Email;
      this.mail = email;

      const envio = {
        text: 'hola',
        node: this.node,
        email: this.mail,
        sessionId: '',
        type: 'user',
      };

      console.log(envio);

      this.chatService.Messenger(envio).then((res: Chat) => {

        const resp = res;

        this.uId = resp.sessionId;
        this.node = resp.node;

        console.log(res);

        if (this.speak === true) {

          responsiveVoice.speak(resp.text[0], 'Spanish Female', { pitch: 1 });

        }

        this.name = this.account.Name;

        const comp = resp.text[0];

        const name = comp.indexOf('_nombre');

        console.log(name);

        if (name !== -1) {

          const text = resp.text[0];


          const nombre = text.replace('_nombre', this.name);

          resp.text = nombre;

          this.mensaje.push(resp);

        } else {

          this.mensaje.push(resp);

        }
      });
    } else {

      alert('llene los campos');

    }


  }



  // chat


  onTaskAdd(event) {
    const textInput = document.getElementById('chat-input');
    (textInput as HTMLInputElement).value = '';
  }


  clicked(mensajes) {

    const envio = {
      text: mensajes,
      node: this.node,
      type: 'user',
      email: this.mail
    };
    console.log(mensajes);
    this.chatService.Messenger(envio).then((res: Chat) => {
      const resp = res;
      const comp = resp.text[0];

      this.node = resp.node;

      const name = comp.indexOf('_nombre');

      console.log(name);

      if (name !== -1) {

        const text = resp.text[0];


        const nombre = text.replace('_nombre', this.name);

        resp.text = nombre;

        this.mensaje.push(envio, resp);

      } else {

        this.mensaje.push(envio, resp);

      }
    });
    console.log(envio);
    return envio;
  }


  generarBubble() {
    const envio = {
      text: (document.getElementById('chat-input') as HTMLInputElement).value,
      sessionId: this.uId,
      node: this.node,
      type: 'user',
      email: this.mail
    };

    if (envio.text === '') {

      alert('por favor poner un mensaje');
      console.log('entro nulo');

    } else {
      this.mensaje.push(envio);
      console.log(envio);
      // responsiveVoice.speak(envio.text, 'Spanish Female', {pitch: 3});

      this.chatService.Messenger(envio).then((res: Chat) => {
        const resp = res;
        const comp = resp.text[0];

        const name = comp.indexOf('_nombre');

        this.node = resp.node;

        console.log(res);

        console.log(name);

        if (name !== -1) {

          const text = resp.text[0];


          const nombre = text.replace('_nombre', this.name);

          resp.text = nombre;

          this.mensaje.push(resp);

        } else {

          this.mensaje.push(resp);

        }

        if (this.speak === true) {

          responsiveVoice.speak(resp.text[0], 'Spanish Female', {pitch: 1});

        }

      });

    }

    this.text = '';

  }


  sumar(numero1, numero2) {
    return numero1 + numero2;
  }

}

